    monospace;
}

/*::-webkit-scrollbar{*/
/*  display: none;*/
/*}*/

/* width */
::-webkit-scrollbar {
  width: 10px;
  background: #333;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  box-shadow: inset 0 0 5px black;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: lightslategray;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: slategray;
}


